.TodoList {
    text-align: center;
    font-family: Arial, sans-serif;
    padding: 20px;
  }
  
  .todoInput {
  margin-right: 8px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  }
  
  .todoButton {
  padding: 5px 10px;
  border: none;
  background-color: #28a745;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  }
  
  .todoButton:hover {
  background-color: #218838;
  }
  
  ul {
  list-style-type: none;
  padding: 0;
  }
  
  .todoList {
  margin: 10px 0;
  padding: 10px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  }
  