/*** Header Css ***/
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*** Nav Css ***/
nav{
  background-color: #333;
  padding: 10px;
}
nav ul{
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
nav ul li{
  margin-right: 20px;
}
nav ul li a{
  color: white;
  text-decoration: none;
  padding: 8px 16px;
  display: block;
}
nav ul li a:hover{
  background-color: #575757;
  border-radius: 4px;
}